/*inputaddonBeforeSelect*/
.ant-input-group-addon:first-child{
	padding: 0 !important;
	border: none !important;
}
.ant-input-group-addon>.ant-select>.ant-select-selection{
	border-top-right-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
	border-top-left-radius: 6px !important;
	border-bottom-left-radius: 6px !important;
}	

/*btn, */
.ant-btn{
	margin:0 5px
}

/*SelectaddonBeforeSelect*/
.ant-input-group>.ant-select>.ant-select-selection--single {
	background-color: white !important;
}
.ant-input-group-addon>.ant-select>.ant-select-selection--single {
	margin-left: -1px;
	height: 32px;
	background-color: #eee;
}

/*  */
.ant-pagination-options>.ant-pagination-options-size-changer>.ant-select-selection{
	width: 100px;
}


